// @TODO: Remove for production and fix empty blocks
// sass-lint:disable no-empty-rulesets

//-----------------------------------------------------------------------------
// Answers ViewTemplate - SASS variables
//-----------------------------------------------------------------------------


//-----------------------------------------------------------------------------
// Answers ViewTemplate - list view                      default.blade.php
//-----------------------------------------------------------------------------

.answers-list {}

//-----------------------------------------------------------------------------
// Answers ViewTemplate - list view item            default_item.blade.php
//-----------------------------------------------------------------------------

// Wrapper for answer item in browse view
.answer-list-item {
  background-color: $body-bg-light;
  margin-top: 12px;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
}

// Answer title (link to item view of answer)
.answer-title {
  color: $brand-primary;
  font-size: $font-size-large;
  font-weight: bold;

  &:hover,
  &:focus {
    color: $brand-info;
    text-decoration: none;
  }
}

// answer body text
.answer-text {
  color: $text-color-dark;
}

// Common class applied to "Upvotes", "Downvotes", "Report Answer",
// and (for users with permission) the "Edit" buttons
.btn-answer {
  border-radius: 5px;
  margin-left: 5px;
}


//-----------------------------------------------------------------------------
// Answers ViewTemplate - item view                             item.blade.php
//-----------------------------------------------------------------------------

.answer-item {
  background-color: $body-bg-light;
  margin-top: 12px;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
}
