//-----------------------------------------------------------------------------
// Core variables and mixins from template
//-----------------------------------------------------------------------------
@import './variables';

$gray: #818078;
$off-white: #fcfcfa;
$black: #000;


//-----------------------------------------------------------------------------
// Component partial includes
//-----------------------------------------------------------------------------

@import 'Site/answers';
@import 'Site/comments';
@import 'Site/image_objects';
@import 'Site/job_postings';
@import 'Site/organizations';
@import 'Site/persons';
@import 'Site/places';
@import 'Site/questions';
@import 'Site/registrations';
@import 'Site/reviews';
@import 'Site/social_icons';

//-----------------------------------------------------------------------------
// General utilities
//-----------------------------------------------------------------------------

// This class is added by validate.js to form fields that are using client-side validation and do not pass
.invalid {
  border-color: $brand-warning !important; // sass-lint:disable-line no-important
}

// Displays HR line on either side of text given by data-content attribute
// <hr class='hr-text' data-content='My Text'>
.hr-text {
  border: 0;
  color: $black;
  height: 1.5em;
  line-height: 1em;
  opacity: .5;
  outline: 0;
  position: relative;
  text-align: center;

  &::before {
    // use a solid background color for a solid bar
    // use the linear-gradient for the fading effect
    background: linear-gradient(to right, transparent, $gray, transparent);
    content: '';
    height: 1px;
    left: 0;
    position: absolute;
    top: 50%;
    width: 100%;
  }

  &::after {
    background-color: $off-white;
    color: $gray;
    content: attr(data-content);
    display: inline-block;
    line-height: 1.5em;
    padding: 0 .5em;
    position: relative;
  }
}

// Set larger font size for SVG output from rater.js star rating module
.rating {
  font-size: 1.5em; /* 30px/20=1.5em */
}
